import React from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";

function Navbar() {
  return (
    <div className='topnav'>
      <Link to='/'>
        <a className='active' href='#home'>
          The News Inquirer
        </a>
      </Link>
      <Link to='/latestnews'>
        <a href='#home'>Home</a>
      </Link>
      <Link to='/search'>
        <a href='#searchtopic'>Search Topic</a>
      </Link>
    </div>
  );
}

export default Navbar;
